import React, { useState } from "react";
import {
  GoogleMap,
  LoadScript,
  Marker,
  useGoogleMap,
} from "@react-google-maps/api";
import BSheet from "./BottomSheet";

const containerStyle = {
  width: "100%",
  height: "100%",
};

function App() {
  const [state, setState] = useState({
    center: { lat: 37.3842, lng: 127.1224 },
    zoom: 17,
  });

  const EventMarkerContainer = ({ position }) => {
    const map = useGoogleMap();

    return (
      <Marker
        position={position}
        onClick={(marker) => {
          map.panTo(marker.latLng);
        }}
      ></Marker>
    );
  };
  const data = [
    {
      content: <div style={{ color: "#000" }}></div>,
      latlng: { lat: 37.3842, lng: 127.1224 },
    },
    {
      content: <div style={{ color: "#000" }}>  </div>,
      latlng: { lat: 37.38521, lng: 127.12076 },
    },
    {
      content: <div style={{ color: "#000" }}>  </div>,
      latlng: { lat: 37.38331, lng: 127.1215 },
    },
    {
      content: <div style={{ color: "#000" }}> </div>,
      latlng: { lat: 37.38605, lng: 127.12399 },
    },
  ];

  const userLocationButton = () => {
    if (navigator.geolocation) {
      // GeoLocation    
      navigator.geolocation.getCurrentPosition(
        (position) => {
          // console.log(position.coords);
          setState((prev) => ({
            ...prev,
            center: {
              lat: position.coords.latitude, // 
              lng: position.coords.longitude, // 
            },
            zoom: 17,
          }));
        },
        (err) => {
          setState((prev) => ({
            ...prev,
            errMsg: err.message,
            isLoading: false,
          }));
        },
        {
          enableHighAccuracy: true,
          // maximumAge: 100000000,
          // timeout: 5000,
        }
      );
    } else {
      alert("    !..");
    }
  };
  return (
    <LoadScript googleMapsApiKey="AIzaSyCSYjuiuUYQ2tYtEE5V26yBzQhc5M6xjPM">
      <GoogleMap
        options={{ streetViewControl: false, minZoom: 14, maxZoom: 18 }}
        mapContainerStyle={containerStyle}
        center={state.center}
        zoom={state.zoom}
        onDragEnd={console.log("hiii")}
      >
        {data.map((value) => (
          <EventMarkerContainer
            key={`EventMarkerContainer-${value.latlng.lat}-${value.latlng.lng}`}
            position={value.latlng}
            content={value.content}
          />
        ))}
        <BSheet userLocationButton={userLocationButton} />
      </GoogleMap>
    </LoadScript>
  );
}

export default React.memo(App);
